.btn-delete {
    position: relative;
    display: block;
    max-width: 300px;
    /* padding: 10px 20px; */
    color: #62bbc1;
    font-weight: 400;
    padding: 0.375rem 0.75rem;
    text-align: center;
    text-decoration: none;
    overflow: hidden;
    transition: color 150ms ease-in-out 150ms, border-color 300ms ease-out,
        box-shadow 300ms ease-in-out;
    z-index: 1;
    background-color: #f1899b !important;
    color: white !important;
}
.btn-delete.center {
    margin: 0 auto;
}
.btn-delete:after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: red;
    z-index: -1;
    transform: scaleX(0);
    transition: transform 300ms ease-out 200ms;
}
.btn-delete:hover {
    color: white;
    border-color: #b9b9b9;
    box-shadow: 0 0 16px rgba(255, 255, 255, 0.1);
}
.btn-delete:hover:after {
    transform: scaleX(1);
    transform-origin: 50% 50%;
    transition: transform 300ms ease-out;
}

