.button {
    touch-callout: none;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    display: inline-block;
    border: 0.2em solid;
    position: relative;
    cursor: pointer;
    overflow: hidden;
    color: #fff;
    background-color: black;
    height: inherit;
}
.button__text {
    display: block;
    padding: 5px 10px;
    text-transform: uppercase;
    font-weight: bold;
}
.button__text:before {
    content: attr(title);
}
.button__text--bis {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transform: translateX(-1em);
    opacity: 0;
}
.button__mask {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: white;
    transform: translateX(-100%) rotate(45deg);
    transition: all 0.3s;
}
.button:hover {
    opacity: 1;
}
.button:hover .button__text {
    animation: fx-text 0.3s ease-out;
}
.button:hover .button__text--bis {
    animation: fx-text-bis 0.3s ease-out;
}
.button:hover .button__mask {
    animation: fx-mask 0.3s ease-out;
}
.button:active {
    opacity: 1;
    background: white;
    color: inherit;
}
@keyframes fx-mask {
    0% {
        transform: translateX(-100%) rotate(45deg);
    }
    100% {
        transform: translateX(100%) rotate(45deg);
    }
}
@keyframes fx-text {
    0% {
        transform: translateX(0);
        opacity: 1;
    }
    100% {
        transform: translateX(1em);
        opacity: 0;
    }
}
@keyframes fx-text-bis {
    0% {
        transform: translateX(-1em);
        opacity: 0;
    }
    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

